<template>

    <tituloBusqueda titulo="Alta Maestros" descripcion="Da de alta a maestros o modifica la informaciÃ³n de los que ya tengas almacenados."></tituloBusqueda>

    <div class="formularios">

        <div class="datos-banco">
            <article>DATOS GENERALES</article>
            <span>Paso 1 de 2</span>
        </div>

        <div class="row">
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="id" @keypress="valida_id" @keyup="mayuscula" class="form-control"
                        aria-describedby="id" placeholder="ID" disabled="true">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="curp" @keypress="valida_curp" @keyup="mayuscula" class="form-control"
                        aria-describedby="curp" placeholder="CURP">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="rfc" @keypress="valida_rfc" @keyup="mayuscula" class="form-control"
                        aria-describedby="rfc" placeholder="RFC">
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4 ">
                <div class="form-group ">
                    <input type="text " v-model="paterno" @keypress="valida_paterno" @keyup="mayuscula"
                        class="form-control " aria-describedby="apellidoPat" placeholder="Apellido Paterno ">
                </div>
            </div>
            <div class="col-md-4 ">
                <div class="form-group ">
                    <input type="text " v-model="materno" @keypress="valida_materno" @keyup="mayuscula"
                        class="form-control " aria-describedby="apeliidoMat" placeholder="Apellido Materno ">
                </div>
            </div>
            <div class="col-md-4 ">
                <div class="form-group ">
                    <input type="text " v-model="nombre" @keypress="valida_nombre" @keyup="mayuscula"
                        class="form-control " aria-describedby="nombre " placeholder="Nombre ">
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4 ">
                <div class="form-group ">
                    <input type="text " v-model="telefono" @keypress="valida_telefono" class="form-control "
                        aria-describedby="apellidoPat " placeholder="Telefono">
                </div>
            </div>
            <div class="col-md-4 ">
                <div class="form-group ">
                    <input type="text " v-model="correo" @keypress="valida_correo" @keyup="mayuscula"
                        class="form-control " aria-describedby="apeliidoMat " placeholder="Correo">
                </div>
            </div>
        </div>
        <!--<div class="row">
                    <div class="col-12 ">
                        <div class="form-group ">
                            <label for=" ">Observaciones generales</label>
                            <textarea class="form-control " id=" " rows="2 "></textarea>
                        </div>
                    </div>
                </div>-->

        <div class="datos-banco">
            <article>DATOS DE INGRESO</article>
            <span>Paso 2 de 2</span>
        </div>

        <div class="row">
            <div class="col">
                <input_seccion @seccion_val="obten_IdMaestro" ></input_seccion>
            </div>
            <!-- <div class="col-md-3">
                <label for=" ">FECHA INGRESO SEP</label>
            </div> -->

            <div class="col">
                <div class="form-group">
                    <input type="text " v-model="qnaingresosep" @keypress="valida_qnaingresosep" @keyup="mayuscula"
                        class="form-control " aria-describedby="qna ingreso " placeholder="QNA INGRESO SEP">
                </div>
            </div>
        </div>
        <div class="">
            <div class="form-group">
                <input type="text " v-model="domicilio" @keypress="valida_domicilio" @keyup="mayuscula"
                    class="form-control " aria-describedby="apeliidoMat " placeholder="DOMICILIO">
            </div>

        </div>

        <div class="buttons ">
            <!--<a class="btn primary " @click="muestraModal">
                Guardar y dar de alta plazas
            </a>-->
            <a class="btn primary " @click="muestraModal">
                Guardar
            </a>
        </div>

    </div>
    <!-- formulario -->

    <miModal titulo='MAESTRO' :mensaje='mensajeModal'></miModal>
    <myFooter></myFooter>

</template>

<script>

import { defineAsyncComponent } from 'vue'
import axios from 'axios'


const input_seccion = defineAsyncComponent(() => import('@/modules/shared/inputs/seccion.vue'))
const miModal = defineAsyncComponent(()=>import('@/modules/shared/components/ModalAceptar.vue'))
const myFooter = defineAsyncComponent(()=>import('@/modules/shared/components/Footer.vue'))
const tituloBusqueda = defineAsyncComponent(()=>import('@/modules/shared/components/TituloBusqueda.vue'))

let id = ''
let curp = ''
let rfc = ''
let paterno = ''
let materno = ''
let nombre = ''
let telefono = ''
let correo = ''
let domicilio = ''
let observaciones = ''
let seccion = ''
let qnaingresosep = ''

    //Variables Modal
    let mensajeModal=''

export default ({

    components: {
        input_seccion,
        miModal,
        myFooter,
        tituloBusqueda
    },

    data() {
        return {
            id,
            curp,
            rfc,
            paterno,
            materno,
            nombre,
            telefono,
            correo,
            seccion,
            domicilio,
            observaciones,
            result:null,
            mensajeModal,
            qnaingresosep
        }
    },
    methods: {

        valida_id(e) {
            if (this.id.length >= 10 )
                e.preventDefault();
        },
        valida_curp(e){
            if (this.curp.length >= 18)
                e.preventDefault()
        },
        valida_rfc(e){
            if(this.rfc.length >= 13)
                e.preventDefault()
        },
        valida_paterno(e){
             this.paterno = this.paterno.toUpperCase();
            if (this.paterno.length >= 25)
                e.preventDefault()
        },
        valida_materno(e){
            if (this.materno.length >= 25)
                e.preventDefault()
        },
        valida_nombre(e){
            if (this.nombre.length >= 25)
                e.preventDefault()
        },
        valida_telefono(e){
            if (this.telefono.length >= 10 || isNaN(parseInt(e.key)))
                e.preventDefault()
        },
        valida_correo(e){
            if (this.correo.length >= 30)
                e.preventDefault()
        },
        valida_domicilio(e){
            if(this.domicilio.length >= 80)
                e.preventDefault()
        },
        valida_observaciones(e){
            if (this.observaciones.length >= 80 )
                e.preventDefault()
        },
        valida_qnaingresosep(e){
            if (this.qnaingresosep.length >= 6 || isNaN(parseInt(e.key)))
                e.preventDefault()
        },

        mayuscula(){
            this.id = this.id.toUpperCase()
            this.curp = this.curp.toUpperCase()
            this.rfc = this.rfc.toUpperCase()
            this.paterno = this.paterno.toUpperCase()
            this.materno = this.materno.toUpperCase()
            this.nombre = this.nombre.toUpperCase()
            this.correo = this.correo.toUpperCase()
            this.domicilio = this.domicilio.toUpperCase()
            this.observaciones = this.observaciones.toUpperCase()
        },
        async obten_IdMaestro(secc){
            console.log("La seccion desde el componente es: ",secc)
            this.seccion = secc
            try{
                const response = await axios.get(`http://localhost:8081/api/maestro_id/${secc}/`)
                this.id = response.data.id_trabajador 

                console.log(response.data.id_trabajador)
            }
            catch(error)
            {
                console.error(error)
            }
        },
        async muestraModal(){
            
            try{
                const resultado = await this.Guardar()
                this.mensajeModal = resultado
                console.log(resultado)
    
            }
            catch(error){
                this.mensajeModal = error.message
                console.log(error)
            }

            $('#staticBackdrop2').modal('show')

        },
        async Guardar(){

            let data = {
                nombre:this.nombre,
                ap_pat:this.paterno,
                ap_mat:this.materno,
                curp:this.curp,
                secc:this.seccion,
                rfc:this.rfc,
                tel:this.telefono,
                correo:this.correo,
                id_trabajador:this.id,
                qna_sep:this.qnaingresosep,
                domicilio:this.domicilio
            }

            try{
                const response = await axios.post(`http://localhost:8081/api/maestro/`,data)
                return response.data.result.msg
            }
            catch(error){
                console.log(error.response.data.msg)
                return error.response.data.msg
            }
        }

    },
    
    

})

</script>